body { 
  padding-top: 65px; 
  font-family: 'Farro', sans-serif;
}

.App {
  text-align: center;
}


.navbar {
  background-color: black;
  justify-content: center;
}

.navbar.center .navbar-inner {
  text-align: center;
}
.navbar.center .navbar-inner .nav {
  float: none;
  display:inline-block;
  vertical-align: top;  
}

.navbar-brand {
  color: white !important;
  font-size: 30px !important;
  font-weight: 700;
  text-transform: uppercase;

}

.nav-link{
  color: white !important;
  text-transform: uppercase;
}

.navbar-light .navbar-toggle.collapsed {
  border-color: white;
}

.navbar-toggler{
  background-color: white;
  margin-left: auto;
}

.navbar-toggler-icon {
  background-color: #fff;
}


.jumbotron {
  background-color: lightgrey;
  margin: 0;
}
.jumbotron-subheading {
  padding-top: 5px;
  font-size: 1.25rem;
}
.page-section { 
  padding: 6rem 0;
}


#about {
  background-color: white;
  margin: 0;
}

 
.table-portfolio{
  margin: auto;
}
.cell-left {
  text-align: left;
}

#portfolio {
  background-color: lightgrey;
}
.portDiv {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: -30px;
}
.portfolio-img{
  height: 200px;
  width: 200px;
}

.social-icon{
  padding-right: 20px;
}

.loading-indicator{
  width: 100%;
  height: "100";
  display: "flex";
  justify-content: "center";
  align-items: "center"
}

.footer {
  background-color: black;
}

@media screen and (max-width: 600px) {
  .navbar-brand {
    font-size: 15px !important;
  }
  body {
    padding-top: 55px; 
  }
  .heading-text {
    font-size: 20px;
  }
  .portfolio-img {
    width: 80px;
    height: 80px;
  }
  .jumbotron-subheading {
    font-size: 20px;
  }
  .page-section { 
    padding: 2rem 0;
  }
  .page-section-header{
    font-size: 20px;
  }
  .about-text{
    font-size: 15px;
    text-align: left;
    padding-left: 20px;
  }
  .table-portfolio{
    font-size: 10px;
  }

}


